.stoimost-container {
  width: 100%;
  padding: 0  10% 2.604vw 10%;
  box-sizing: border-box;
  background-color: #FFFFFF;
}

.stoimost-content {
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  background: rgba(235, 235, 235, 1);
  border-radius: 1.042vw;
  padding: 0.521vw 0.781vw 0.521vw 2.604vw;
  display: flex;
  align-items: center;
  gap: 2.604vw;
  position: relative;
  overflow: hidden;
  min-width: 0;
}

.stoimost-woman-image {
  flex-shrink: 0;
  width: 18.021vw;
  height: 11.458vw;
  min-width: 14.583vw;
  max-width: 18.021vw;
  border-radius: 1.042vw;
  overflow: hidden;
  margin-left: -2.083vw;
  position: relative;
  z-index: 1;
  border: 0.15vw solid rgba(255, 255, 255, 1);
  transition: transform 0.3s ease;
}

.stoimost-woman-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center 20%;
  display: block;
}

.stoimost-woman-image:hover {
  transform: translateX(3px);
}

.stoimost-text-block {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.521vw;
  z-index: 2;
  min-width: 0;
}

.stoimost-title {
  text-align: left;
  font-size: var(--font-size-h5);
  font-weight: 700;
  color: #000000;
  margin: 0;
  line-height: 1.2;
  font-family: 'Geist', sans-serif;
}

.stoimost-description {
  text-align: left;
  font-size: var(--font-size-h8);
  font-weight: 300;
  color: #1A1A1ABF;
  margin: 0;
  line-height: 1.74vw;
  font-family: 'Geist', sans-serif;
}

.stoimost-button {
  padding: 0.313vw 0.313vw 0.313vw 1.563vw;
  border-radius: 2.604vw;
  border: 0.052vw solid #2E3D58;
  font-weight: 700;
  font-size: var(--font-size-button);
  line-height: 0.885vw;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 1.563vw;
  font-family: 'Geist', sans-serif;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}

.stoimost-btn-primary {
  background: #485B85;
  color: #FFFFFF;
}

.stoimost-btn-primary span {
  color: #FFFFFF;
  transition: color 0.3s ease, transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  z-index: 3;
}

.stoimost-btn-primary:hover span {
  color: #485B85;
  transform: translateX(3.567vw);
}

.stoimost-btn-icon {
  width: 3.125vw;
  height: 3.125vw;
  border-radius: 50%;
  background: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
  z-index: 2;
}

.stoimost-btn-icon svg {
  width: 0.73vw;
  height: 0.63vw;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.stoimost-button:hover .stoimost-btn-icon svg {
  transform: translateX(-12.0vw) rotate(40deg);
}

.stoimost-button::after {
  content: '';
  position: absolute;
  right: 0.313vw;
  top: 50%;
  transform: translateY(-50%);
  width: 3.125vw;
  height: 3.125vw;
  border-radius: 50%;
  background: #FFFFFF;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 0;
  pointer-events: none;
}

.stoimost-button:hover::after {
  width: 41.667vw;
  height: 41.667vw;
  right: -20.833vw;
  top: 50%;
  transform: translateY(-50%);
}

/*     */
.stoimost-button .flare {
  position: absolute;
  top: 0;
  height: 100%;
  width: 45px;
  transform: skewX(-45deg);
  animation: flareAnimation 3s infinite;
  left: -170%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.9), #fff);
  filter: blur(0px);
  z-index: 1;
  pointer-events: none;
}

@keyframes flareAnimation {
  0% {
    left: -170%;
  }
  50% {
    left: 170%;
  }
  100% {
    left: 170%;
  }
}

@media (max-width: 2200px) { 
  .stoimost-container {
    padding: 0 2.6vw 4.2vw 2.6vw;
  }
}